import React from "react";

import "./section.css";

export default class Section extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <div className={`section`}>
        <div className={`section-wrapping ${this.props.className}`}>
          {this.props.title ? <h2 className={`${this.props.titleClassName?this.props.titleClassName:""}`}>{this.props.title}</h2> : ""}
          {this.props.children}
        </div>
      </div>
    );
  }
}



