import React from "react";
import { Route, Switch } from "react-router-dom";

import HomePage from "./../../page/homepage/index.js";
import TermsPage from "./../../page/termspage/index.js";
// import ServicePage from "./../../page/servicepage/index.js";
import PricingPage from "./../../page/pricingpage/index.js";
import PrivacyPage from "./../../page/privacypage/index.js";
import ContactPage from "./../../page/contactpage/index.js";
// import FAQPage from "./../../page/faqpage/index.js";
// import NewsPage from "./../../page/newspage/index.js";
// import FacilitiesPage from "./../../page/facilitiespage/index.js";

export default class RouterList extends React.Component {
  render() {
    return (
      <div className="main_wrapper">
        <Switch>
          <Route exact path="/" component={HomePage} />
          <Route path="/terms" component={TermsPage} />
          <Route path="/privacy" component={PrivacyPage} />
          {/* <Route path="/service" component={ServicePage} /> */}
          <Route path="/pricing" component={PricingPage} />
          <Route path="/contact" component={ContactPage} />
          {/* <Route path="/faq" component={FAQPage} /> */}
          {/* <Route path="/news" component={NewsPage} /> */}
          {/* <Route path="/facilities" component={FacilitiesPage} /> */}
        </Switch>
      </div>
    );
  }
}



