import React from "react";

import "./checkbox.css";

export default class CheckBox extends React.Component {
  constructor(props) {
    super(props);
    this.common = window.common;
    var toSetState = { ...this.props };
    this.state = toSetState;
  }

  checkBoxOnClicked(e) {
    var that = this;
    var { selected, onClick } = that.state;
    var tmpObj = {};
    tmpObj.selected = selected ? false : true;
    onClick(tmpObj);
    this.setState(tmpObj);
  }

  render() {
    var that = this;
    var { selected } = that.state;
    return (
      <div className="checkbox">
        <div className="checkbox-inner" onClick={that.checkBoxOnClicked.bind(that)}>
          {selected ? <div className="checkbox-selected" /> : ""}
        </div>
      </div>
    );
  }
}



