import React from "react";

import Grid from './../../widgets/grid/index.js';
import './introduction.css';

export default class Introduction extends React.Component {
  constructor(props) {
    super(props);
    this.data = {};
    this.data.layouts = {};
    this.data.layouts.tablet = [];
    this.data.layouts.tablet[0] = { i: 'a', x: 0, y: 0, w: 8, h: 2, static: true };
    this.data.layouts.tablet[1] = { i: 'b', x: 8, y: 0, w: 4, h: 2, static: true };
    this.data.layouts.tablet[2] = { i: 'c', x: 0, y: 2, w: 4, h: 4, static: true };
    this.data.layouts.tablet[3] = { i: 'd', x: 4, y: 2, w: 4, h: 2, static: true };
    this.data.layouts.tablet[4] = { i: 'e', x: 8, y: 2, w: 4, h: 2, static: true };
    this.data.layouts.tablet[5] = { i: 'f', x: 4, y: 4, w: 8, h: 2, static: true };
    this.data.layouts.mobile = [];
    this.data.layouts.mobile[0] = { i: 'a', x: 0, y: 0, w: 6, h: 1, static: true };
    this.data.layouts.mobile[1] = { i: 'b', x: 0, y: 1, w: 6, h: 1, static: true };
    this.data.layouts.mobile[2] = { i: 'c', x: 0, y: 2, w: 6, h: 1, static: true };
    this.data.layouts.mobile[3] = { i: 'd', x: 0, y: 3, w: 6, h: 1, static: true };
    this.data.layouts.mobile[4] = { i: 'e', x: 0, y: 4, w: 6, h: 1, static: true };
    this.data.layouts.mobile[5] = { i: 'f', x: 0, y: 5, w: 6, h: 1, static: true };
    this.data.breakpoints = {};
    this.data.breakpoints.tablet = 999;
    this.data.breakpoints.mobile = 767;
    this.data.cols = {};
    this.data.cols.tablet = 12;
    this.data.cols.mobile = 6;
    this.data.bannerArrays = [];
  }





  render() {
    var that = this;
    // var { common } = window;
    // var { lang } = common.data;
    var { layouts, breakpoints, cols } = that.data;
    return (
      <div className="introduction">
        {/* <h2 className="dh-h2">{that.props.title}</h2>
        <p>{that.props.description}</p> */}

        <Grid layouts={layouts} cols={cols} breakpoints={breakpoints}>

          <div key={'a'} className="grid_items">
            <div className={`grid_items_inner`}>
              <div style={{ backgroundImage: `url('${'./assets/images/banners/intro_playstation.jpeg'}')` }} className="grid_items_inner_img" />
            </div>
          </div>

          <div key={'b'} className="grid_items">
            <div className={`grid_items_inner`}>
              <div style={{ backgroundImage: `url('${'./assets/images/banners/intro_poker.jpeg'}')` }} className="grid_items_inner_img" />
            </div>
          </div>

          <div key={'c'} className="grid_items">
            <div className={`grid_items_inner`}>
              <div style={{ backgroundImage: `url('${'./assets/images/banners/intro_vr.jpeg'}')` }} className="grid_items_inner_img" />
            </div>
          </div>

          <div key={'d'} className="grid_items">
            <div className={`grid_items_inner`}>
              <div style={{ backgroundImage: `url('${'./assets/images/banners/intro_monopoly.jpeg'}')` }} className="grid_items_inner_img" />
            </div>
          </div>

          <div key={'e'} className="grid_items">
            <div className={`grid_items_inner`}>
              <div style={{ backgroundImage: `url('${'./assets/images/banners/intro_pizza.jpg'}')` }} className="grid_items_inner_img" />
            </div>
          </div>

          <div key={'f'} className="grid_items">
            <div className={`grid_items_inner`}>
              <div style={{ backgroundImage: `url('${'./assets/images/banners/intro_happy.jpg'}')` }} className="grid_items_inner_img" />
            </div>
          </div>

        </Grid>


        {/* <div className="introduction-boxes">
          {
            that.props.content.map(function (v, i) {
              return (
                <div key={i} className="introduction-boxes-box">
                  <div className="introduction-boxes-box-image" style={{"backgroundImage":`url(${v})`}}/>
                </div>
              )
            })
          }
        </div> */}





      </div>
    )
  }

}


