import React from "react";

import { Link } from "react-router-dom";
// import MenuIcon from "react-icons/lib/md/menu";
// import InfoIcon from "react-icons/lib/md/info-outline";

export default class MenuStatus extends React.Component {
  constructor(props) {
    super(props);
    this.common = window.common;
    this.logoClass = "logo";
  }

  componentDidMount() {
    var that = this;
    that.common.bindWindowsEvent("scroll", that.logoController.bind(that));
  }

  componentWillUnmount() {
    var that = this;
    that.common.removeWindowsEvent("scroll", that.logoController.bind(that));
  }

  logoController(e) {
    var that = this;
    if(window.pageYOffset>300){
      that.logoClass = "logo smaller"
    }else{
      that.logoClass = "logo";
    }
    that.forceUpdate();
  }

  render() {
    var that = this;
    var { lang } = that.common.data;
    var logoIMG = "./assets/images/common/logo_black.svg";
    return (
      <div className="menu-statusbar">
        <div className="menu-wrapper">
          <div className="menu-wrapper-content">
            <div className="menu-wrapper-content-cell">
              <div className={that.logoClass}>
                <Link to='/'>
                  <div className="logo-image" style={{ "backgroundImage": `url("${logoIMG}")` }}></div>
                </Link>
              </div>
            </div>
            <div className="menu-wrapper-content-cell">
              <div className="float right">

                {/* <div className="float left option"><span><Link to='/news'>{lang.menu_option_news}</Link></span></div> */}
                <div className="float left option"><span><Link to='/pricing'>{lang.menu_option_pricing}</Link></span></div>
                {/* <div className="float left option"><span><Link to='/service'>{lang.menu_option_service}</Link></span></div> */}
                {/* <div className="float left option"><span><Link to='/facilities'>{lang.menu_option_facilities}</Link></span></div> */}
                <div className="clearfix" />
              </div>
              <div className="clearfix" />
            </div>
          </div>

          <div className="clearfix" />
        </div>
        <div className="menu-wrapper space">
          <div className="logo"></div>
        </div>
      </div>
    );
  }
}


