import React from "react";
import DocumentTitle from "react-document-title";

import ListTerm from "./../../components/listterm/index.js";
import PageIndex from "./../../widgets/pageindex/index.js";

import "./termspage.css";

export default class TermsPage extends React.Component {
  constructor(props) {
    super(props);
    this.common = window.common;
  }

  render() {
    var that = this;
    var { lang } = that.common.data;
    var pageindex_array = [];
    pageindex_array.push({ label: lang.homepage_title, value: "/" });
    pageindex_array.push({ label: lang.termspage_title, value: "/terms" });

    var terms_array = [];
    terms_array.push({
      title: lang.termspage_section_change_title,
      content: lang.termspage_section_change_content
    });
    terms_array.push({
      title: lang.termspage_section_cancel_title,
      content: lang.termspage_section_cancel_content
    });
    terms_array.push({
      title: lang.termspage_section_weather_title,
      content: lang.termspage_section_weather_content
    });
    terms_array.push({
      title: lang.termspage_section_rules_title,
      content: lang.termspage_section_rules_content
    });

    return (
      <DocumentTitle title={`${lang.termspage_title}${lang.document_title}`}>
        <div className="termspage">
          <div className="dh-part">
            <PageIndex data={pageindex_array} />
          </div>
          <div>
            <div className="dh-part">
              <ListTerm data={terms_array} />
            </div>
          </div>
        </div>
      </DocumentTitle>
    );
  }
}



