import React from "react";

import './intro.css';

export default class Intro extends React.Component {
  constructor(props) {
    super(props);
    this.data = {};
  }

  render() {
    var that = this;
    return (
      <div className="intro">
        <h2 className="dh-h2"> {that.props.title} </h2>
        <p>{that.props.description}</p>
      </div>
    )
  }
}


