import React from "react";
import DocumentTitle from "react-document-title";
import Slides from './../../components/slides/index.js';

import Intro from './../../components/intro/index.js';
import Introduction from './../../components/introduction/index.js';

import "./homepage.css";

export default class HomePage extends React.Component {
  constructor(props) {
    super(props);
    this.common = window.common;
  }


  render() {
    var that = this;
    var { lang } = that.common.data;
    var imagesArr = [];
    imagesArr[0] = './assets/images/banners/1.jpg';
    imagesArr[1] = './assets/images/banners/2.jpg';
    imagesArr[2] = './assets/images/banners/3.jpg';
    imagesArr[3] = './assets/images/banners/4.jpg';
    imagesArr[4] = './assets/images/banners/5.jpg';
    imagesArr[5] = './assets/images/banners/6.jpg';

    var introductionImages = [];
    introductionImages[0] = './assets/images/common/switch.png';
    introductionImages[1] = './assets/images/common/ps4.png';
    introductionImages[2] = './assets/images/common/boardgames.png';
    introductionImages[3] = './assets/images/common/poker.png';
    introductionImages[4] = './assets/images/common/guitar.png';



    return (
      <DocumentTitle title={`${lang.homepage_title}${lang.document_title}`}>
        <div className="homepage" >
          <Slides value={imagesArr} type="slide"/>
          <Intro title={lang.homepage_introduction} description={lang.homepage_introduction_description}/>
          <Introduction content={introductionImages}/>
        </div>
      </DocumentTitle>
    );
  }
}



