import React from "react";
import { Responsive, WidthProvider } from 'react-grid-layout';

import './grid.css';

import './../../../../node_modules/react-grid-layout/css/styles.css'
import './../../../../node_modules/react-resizable/css/styles.css'


export default class Grid extends React.Component {
  constructor(props) {
    super(props);
    this.GridLayout = WidthProvider(Responsive);
    this.state = {};
  }

  render() {
    var that = this;
    var { children , layouts, breakpoints , cols} = that.props;
    var ResponsiveReactGridLayout = that.GridLayout;

    return (
      <div className="grid">
        <ResponsiveReactGridLayout className="layout" layouts={layouts} breakpoints={breakpoints} cols={cols}>
          {children}
        </ResponsiveReactGridLayout>
      </div>
    );
  }
}



