import React from "react";
import PropTypes from "prop-types";
import { Carousel } from "react-responsive-carousel";

import "./../../../../node_modules/react-responsive-carousel/lib/styles/carousel.min.css";
import "./slides.css";

class Slides extends React.Component {
  constructor(props) {
    super(props);
    this.common = window.common;
    this.loop = true;
    this.autoplay = true;
    this.slides = [];
  }

  componentDidMount() {
    if (this.props.type === "slide") {
      for (var a in this.props.value) {
        this.slides.push(this.props.value[a]);
      }
    }
    this.forceUpdate();
  }

  render() {
    var that = this;
    var type = {};
    type.slide = (
      <Carousel
        autoPlay={that.autoplay}
        infiniteLoop={that.loop}
        showStatus={false}
        emulateTouch={true}
        centerMode={true}
        interval={100000}
      >
        {that.slides.map(function(v, i) {
          return (
            <div className="slide" key={i}>
              <div
                className="slideContent"
                style={{ backgroundImage: `url(${v})` }}
              />
            </div>
          );
        })}
      </Carousel>
    );
    type.video = (
      <div className="slides-video">
        <video
          src={that.props.value}
          autoPlay={that.autoplay}
          loop={that.loop}
        />
      </div>
    );
    return <div className="slides">{type[that.props.type]}</div>;
  }
}

Slides.propTypes = {
  type: PropTypes.string
};

export default Slides;



