import React from "react";
import RouterList from "./../router/index.js";

import { withRouter } from "react-router-dom";


import Menu from "./../menu/index.js";
import Footer from "./../footer/index.js";

import CommonClass from "./../../global/class/common.js";
import "./app.css";

class App extends React.Component {
  constructor(props) {
    super(props);
    var toSetState = {};
    toSetState.common = new CommonClass();
    this.state = toSetState;
  }
  beforeReloadPage() {
    var that = this;
    var { localstorage } = that.state.common.classes;
    var tempObj = {};
    tempObj.form = that.state.common.data.form;
    localstorage.store(tempObj);
  }
  commonLoad() {
    var that = this;
    that.state.common.initConfig(this.props.config, function (common) {
      window.common = common;
      window.common.storeComponent("app", that);
      window.common.bindWindowsEvent("beforeunload", that.beforeReloadPage.bind(that));
      that.forceUpdate();
    });
  }
  componentDidMount() {
    this.commonLoad();
  }
  componentWillMount() {
    var that = this;
    that.unlisten = this.props.history.listen(function (location, action) {
      document.body.scrollTop = 0;
      document.documentElement.scrollTop = 0;
    });
  }
  componentWillUnmount() {
    var that = this;
    that.unlisten();
  }

  render() {
    var { common } = window;
    return common ? (
      <div className="App">
        <Menu />
        <RouterList />
        <Footer />
      </div>
    ) : "";
  }
}

export default withRouter(App);


