import Axios from "axios";

export default class commonModel {
  constructor(host) {
    this.data = {};
    this.data.host = host;
  }

  getData(path, obj) {
    var { host } = this;
    var getParams = "?";
    if(obj){
      for(var a in obj){
        getParams+=a;
        getParams+="=";
        getParams+=obj[a];
        getParams+="&";
      }
    }
    console.log(`${host}${path}${getParams}`)
    console.log(obj)


    return obj
      ? Axios.get(`${host}${path}${getParams}`).then(function(res) {
          return res.data;
        })
      : Axios.get(`${host}${path}`).then(function(res) {
          return res.data;
        });
  }

  postData(path, obj) {
    var { host } = this;
    return obj
      ? Axios.post(`${host}${path}`, obj).then(function(res) {
          return res.data;
        })
      : Axios.post(`${host}${path}`).then(function(res) {
          return res.data;
        });
  }
}



