import CommonModel from "./commonmodel.js";

export default class NewsModel extends CommonModel {
  constructor(host) {
    super(host);
    this.data = {};
    this.host = host;
  }

  list(obj) {
    var that = this;
    var toGet = obj
      ? that.getData("/assets/data/news.json", obj)
      : that.getData("/assets/data/news.json");
    return toGet.then(function(data) {
      var toReturn = {};
      toReturn.data = [];
      for (var a in data.data) {
        toReturn.data.push(data.data[a]);
        toReturn.data[a].url = `/news/${data.data[a].id}`;
      }
      return toReturn.data;
    });
  }
}



