import React from "react";

import Section from "./../../widgets/section/index.js";

import "./listterm.css";

export default class TermsPage extends React.Component {
  constructor(props) {
    super(props);
    this.common = window.common;
  }

  render() {
    var that = this;

    return (
      <div className="listterm">
        {that.props.data.length !== 0
          ? that.props.data.map(function(v, i) {
              return (
                <Section
                  title={v.title}
                  className="listterm-term"
                  titleClassName="dh-h3"
                  key={i}
                >
                  <div
                    dangerouslySetInnerHTML={{
                      __html: v.content
                    }}
                  />
                </Section>
              );
            })
          : ""}
      </div>
    );
  }
}



