import React from "react";

import { Link } from "react-router-dom";


export default class MenuContent extends React.Component {
  constructor(props) {
    super(props);
    this.common = window.common;
    this.animatedClass = "";
  }

  componentDidMount() {
    var that = this;
    that.animatedClass = "pop";
    that.forceUpdate();
  }

  render() {
    var that = this;
    var { lang } = that.common.data;
    return (
      <div className="menu-content">
        <div className={`menu-content-wrapper ${that.animatedClass}`}>
          <div className="menu-content-header">
            <div className="float left">
              <h2 className="dh-h2">{lang.menu_title}</h2>
            </div>
            <div className="float right" onClick={that.props.onClick}>
              <h2 className="dh-h2 option">Close</h2>
            </div>
            <div className="clearfix"></div>
          </div>

          <div className="menu-content-list">

            <div className="menu-content-group">
              <h4 className="dh-h4" onClick={that.props.onClick}>
                <Link to='/news' className="option">{lang.menu_option_news}</Link>
              </h4>
              <h4 className="dh-h4" onClick={that.props.onClick}>
                <Link to='/pricing' className="option">{lang.menu_option_pricing}</Link>
              </h4>
              <h4 className="dh-h4" onClick={that.props.onClick}>
                <Link to='/service' className="option">{lang.menu_option_service}</Link>
              </h4>
              <h4 className="dh-h4" onClick={that.props.onClick}>
                <Link to='/facilities' className="option">{lang.menu_option_facilities}</Link>
              </h4>
            </div>

          </div>

        </div>
      </div>
    );
  }
}


