import Axios from "axios";
import Cookies from "universal-cookie";
import LocalStorage from "./localstorage.js";
import * as Scroll from "react-scroll";

import UserModel from "./../model/usermodel.js";
import NewsModel from "./../model/newsmodel.js";

// import Mouse from "./mouse.js";

export default class Common {
  constructor() {
    this.classes = {};
    this.classes.cookies = new Cookies();
    this.classes.localstorage = new LocalStorage();
    this.classes.scroll = Scroll;
    // this.classes.mouse = new Mouse();
    this.data = {};
    this.data.form = {};
    this.data.model = {};
    this.component = {};
  }

  bodyStyle(obj) {
    Object.assign(document.body.style, obj);
  }
  makeid(myLen) {
    var text = "";
    var possible = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
    for (var i = 0; i < myLen; i++) {
      text += possible.charAt(Math.floor(Math.random() * possible.length));
    }
    return text;
  }
  createNewForm(name) {
    this.data.form[name] = {};
  }
  formValueChanged(formName, valueObj) {
    Object.assign(this.data.form[formName], valueObj);
  }
  checkFormValue(formName, fieldname) {
    var toReturn = true;
    if (this.data.form[formName]) {
      if (!this.data.form[formName][fieldname]) {
        toReturn = false;
      } else {
        if (this.data.form[formName][fieldname].length === 0) {
          toReturn = false;
        }
      }
    } else {
      toReturn = false;
    }
    return toReturn;
  }
  changeLanguage(lang, component) {
    var cookiesToSet = {};
    cookiesToSet.lang = lang;
    this.setCookies(cookiesToSet);
    this.component.app.beforeReloadPage();
    this.component.app.commonLoad();
  }
  storeComponent(name, component) {
    this.component[name] = component;
  }
  storeModel(name, model) {
    this.data.model[name] = model;
  }
  setCookies(obj, config) {
    for (var a in obj) {
      this.classes.cookies.set(a, obj[a], config);
    }
  }
  removeCookies(name, config) {
    this.classes.cookies.remove(name, config);
  }
  getAllCookies() {
    this.data.cookies = this.classes.cookies.getAll();
    return this.data.cookies;
  }
  transformData(key, config) {
    var that = this;
    var methods = {};
    methods.lang = function () {
      return that.data.lang[config];
    };
    methods.env = function () {
      return that.data.env[config];
    };
    methods.form = function () {
      var toReturn = {};
      for (var a in config) {
        for (var b in config[a]) {
          if (b.length) {
            toReturn = config;
          }
        }
      }
      return toReturn;
    };
    return methods[key]();
  }
  bindWindowsEvent(event, func) {
    window.addEventListener(event, function (e) {
      func(e);
    });
  }
  removeWindowsEvent(event, func) {
    window.removeEventListener(event, func);
  }
  initModels() {
    var { api } = this.data.env;
    this.storeModel("user", new UserModel(api));
    this.storeModel("news", new NewsModel(api));
  }
  initValues() {
    var that = this;
    that.getAllCookies();
    if (!that.data.cookies.lang) {
      var { lang } = that.data.default;
      var cookiesToSet = {};
      cookiesToSet.lang = lang;
      that.setCookies(cookiesToSet, { path: "/" });
    }
    that.data.lang = that.transformData("lang", that.getAllCookies().lang);
    that.data.env = that.transformData("env", that.data.default.env);
    that.data.form = that.transformData("form", that.classes.localstorage.get("form"));
  }
  initConfig(path, cb) {
    var that = this;
    Axios.get(path).then(function (res) {
      var toReq = [];
      var toReqName = [];
      var { data } = res;
      for (var a in data.toFetch) {
        toReqName.push(a);
        toReq.push(Axios.get(data.toFetch[a]));
      }
      Axios.all(toReq).then(
        Axios.spread(function (...perms) {
          for (var a in perms) that.data[toReqName[a]] = perms[a].data;
          that.initValues();
          that.initModels();
          cb(that);
        })
      );
    });
  }
}



