import React from "react";
import DocumentTitle from "react-document-title";

import ListTerm from "./../../components/listterm/index.js";
import PageIndex from "./../../widgets/pageindex/index.js";

import "./privacypage.css";

export default class PrivacyPage extends React.Component {
  constructor(props) {
    super(props);
    this.common = window.common;
  }

  render() {
    var that = this;
    var { lang } = that.common.data;
    var pageindex_array = [];
    pageindex_array.push({ label: lang.homepage_title, value: "/" });
    pageindex_array.push({ label: lang.privacypage_title, value: "/privacy" });

    var privacy_array = [];
    privacy_array.push({
      title: lang.privacypage_section_area_title,
      content: lang.privacypage_section_area_content
    });
    privacy_array.push({
      title: lang.privacypage_section_method_title,
      content: lang.privacypage_section_method_content
    });
    privacy_array.push({
      title: lang.privacypage_section_protection_title,
      content: lang.privacypage_section_protection_content
    });
    privacy_array.push({
      title: lang.privacypage_section_url_title,
      content: lang.privacypage_section_url_content
    });
    privacy_array.push({
      title: lang.privacypage_section_cookie_title,
      content: lang.privacypage_section_cookie_content
    });
    privacy_array.push({
      title: lang.privacypage_section_edit_title,
      content: lang.privacypage_section_edit_content
    });

    return (
      <DocumentTitle title={`${lang.privacypage_title}${lang.document_title}`}>
        <div className="privacypage">
          <div className="dh-part">
            <PageIndex data={pageindex_array} />
          </div>
          <div>
            <div className="dh-part">
              <ListTerm data={privacy_array} />
            </div>
          </div>
        </div>
      </DocumentTitle>
    );
  }
}



