import React from "react";

import MenuStatus from './menustatus.js';
import MenuContent from './menucontent.js';

import "./menu.css";

export default class Menu extends React.Component {
  constructor(props) {
    super(props);
    var toSetState = {};
    toSetState.menuOpened = false;
    this.common = window.common;
    this.common.storeComponent('menu', this);
    this.state = toSetState;
  }

  menuBtnClicked(e) {
    this.setState({ menuOpened: this.state.menuOpened ? false : true });
  }

  render() {
    var that = this;
    that.state.menuOpened?this.common.bodyStyle({ overflow: "hidden" }):this.common.bodyStyle({ overflow: "" });

    return (
      <div className="menu">
        <MenuStatus onClick={that.menuBtnClicked.bind(that)} />
        {that.state.menuOpened ? <MenuContent onClick={that.menuBtnClicked.bind(that)} className="pop" /> : ""}
      </div>
    );
  }
}


