import React from "react";
import CheckBox from './../checkbox/index.js';
import {Link} from 'react-router-dom';

import "./tablelist.css";

export default class TableList extends React.Component {
  constructor(props) {
    super(props);
    var that = this;
    var { records, selectable, selectableLabel } = that.props;
    this.common = window.common;
    var toSetState = {};
    toSetState.keys = 0;
    if (records) {
      if (records.length) {
        toSetState.keys = that.countObjKey(records[0]);
        if (selectableLabel || selectable) {
          toSetState.keys++;
        }
      }
    }
    this.state = toSetState;
  }

  checkBoxOnClicked(data, num, name, e) {
    var that = this;
    var { onSelected, records } = that.props;
    var toReturn = {};
    toReturn.target = {};
    var tmpArray = records.slice(0);
    var tmpObject = {};
    Object.assign(tmpObject, data);
    Object.assign(tmpObject, e);
    tmpArray[num] = tmpObject;
    toReturn.target.value = tmpArray;
    toReturn.target.name = name;
    onSelected(toReturn);
  }

  countObjKey(obj) {
    var toReturn = 0;
    // eslint-disable-next-line
    for (var a in obj) toReturn++;
    return toReturn;
  }


  mapObject(object, callback) {
    return Object.keys(object).map(function (key) {
      return callback(object[key], key);
    });
  }

  render() {
    var that = this;
    var { lang } = that.common.data;
    var { records, selectable, selectableLabel, labelPrefix, name } = that.props;

    return (
      <div className="tablelist">
        {records ? records.length ?
          <div>
            <div className="tablelist-record-title">
              <div className="tablelist-record-wrapper">
                {that.mapObject(records[0], function (v, i) {
                  return i !== 'selected' ? <div className="tablelist-record-single" key={i} style={{ width: `${100 / that.state.keys}%` }}>{lang[`${labelPrefix ? labelPrefix : ""}${i}`] || i}</div> : "";
                })}
                {selectableLabel || selectable ? <div className="tablelist-record-single" style={{ width: `${100 / that.state.keys}%` }}>{selectableLabel}</div> : ""}
              </div>
            </div>
            {records.map(function (v, i) {
              return (
                <div key={i} className="tablelist-record">
                {that.props.urlPrefix&&that.props.urlKey?
                  <div className="tablelist-record-wrapper">
                    <div style={selectableLabel || selectable ? { cursor: 'pointer' } : {}}>
                    <Link to={`${that.props.urlPrefix}/${v[that.props.urlKey]}`}>
                      {that.mapObject(v, function (v2, i2) {
                        return i2 !== 'selected' ? <div className="tablelist-record-single" key={i2} style={{ width: `${100 / that.state.keys}%` }}>{v2.toString()}</div> : "";
                      })}
                       </Link>
                      {selectableLabel || selectable ? <div className="tablelist-record-single" style={{ width: `${100 / that.state.keys}%` }}>
                        <CheckBox onClick={that.checkBoxOnClicked.bind(that, v, i, name)} selected={v.selected} />
                      </div> : ""}
                    </div>
                  </div>
                :
                <div className="tablelist-record-wrapper">
                    <div style={selectableLabel || selectable ? { cursor: 'pointer' } : {}}>
                      {that.mapObject(v, function (v2, i2) {
                        return i2 !== 'selected' ? <div className="tablelist-record-single" key={i2} style={{ width: `${100 / that.state.keys}%` }}>{v2.toString()}</div> : "";
                      })}
                      {selectableLabel || selectable ? <div className="tablelist-record-single" style={{ width: `${100 / that.state.keys}%` }}>
                        <CheckBox onClick={that.checkBoxOnClicked.bind(that, v, i, name)} selected={v.selected} />
                      </div> : ""}
                    </div>
                  </div>
                }
                  
                </div>
              )
            })}


          </div>
          : "" : ""}
      </div>
    );
  }
}



