import React from "react";
import { Link } from "react-router-dom";

import "./footer.css";

export default class Footer extends React.Component {
  constructor(props) {
    super(props);
    this.common = window.common;
  }

  render() {
    var { common } = window;
    var { lang } = common.data;
    return (
      <div className="footer">
        <div className="inner_wrapping">
          <div className="footer-menu">
            {/* <h5 className="dh-h5">
              <Link to='/faq' className="footer-menu-option">{lang.footer_menu_faq} </Link>
            </h5> */}
            <h5 className="dh-h5">
              <Link to='/terms' className="footer-menu-option"> {lang.footer_menu_agreement}</Link>
            </h5>
            <h5 className="dh-h5">
              <Link to='/privacy' className="footer-menu-option"> {lang.footer_menu_privacy}</Link>
            </h5>
            <h5 className="dh-h5">
              <Link to='/contact' className="footer-menu-option"> {lang.footer_menu_contact}</Link>
            </h5>
          </div>
          <div className="footer-copyright">
            {lang.footer_copyright}
          </div>

        </div>
      </div>
    );
  }
}




