import React from "react";

import { Link } from "react-router-dom";

import './pageindex.css';

export default class PageIndex extends React.Component {
  constructor(props) {
    super(props);
    this.data = {};
  }

  prepareIndex(data) {
    var toReturn = [];
    var currentComponent = 0;
    for (var a in data) {
      if (a !== '0') {
        currentComponent++;
        toReturn.push(<span key={currentComponent} className="pageindex-item">/</span>);
      }
      currentComponent++;

      toReturn.push(typeof data[a]==='object'?<Link key={currentComponent} to={data[a].value} className="pageindex-item">{data[a].label}</Link>:<span key={currentComponent} className="pageindex-item">{data[a]}</span>);
    }
    return toReturn;
  }

  render() {
    return (
      <div className="pageindex">
        {this.prepareIndex(this.props.data)}
      </div>
    );
  }
}


